'use client'

import Link from 'next/link'
import { usePathname } from 'next/navigation'

// Digital Hub Tab Configuration - follows Marketing/Lead Engine pattern
const DIGITAL_HUB_TABS = [
  { id: 'overview', label: 'Overview', icon: 'fa-chart-line', href: '/sub-account/digital-hub' },
  { id: 'funnels', label: 'Funnels', icon: 'fa-filter', href: '/sub-account/digital-hub/funnels' },
  { id: 'websites', label: 'Websites', icon: 'fa-globe', href: '/sub-account/digital-hub/websites' },
  { id: 'stores', label: 'Stores', icon: 'fa-shopping-cart', href: '/sub-account/digital-hub/stores' },
  { id: 'webinars', label: 'Webinars', icon: 'fa-video', href: '/sub-account/digital-hub/webinars' },
  { id: 'forms', label: 'Forms', icon: 'fa-file-alt', href: '/sub-account/digital-hub/forms' },
  { id: 'surveys', label: 'Surveys', icon: 'fa-poll', href: '/sub-account/digital-hub/surveys' },
  { id: 'quizzes', label: 'Quizzes', icon: 'fa-question-circle', href: '/sub-account/digital-hub/quizzes' },
  { id: 'chat-widget', label: 'Chat Widget', icon: 'fa-comments', href: '/sub-account/digital-hub/chat-widget' },
  { id: 'qr-codes', label: 'QR Codes', icon: 'fa-qrcode', href: '/sub-account/digital-hub/qr-codes' },
  { id: 'domains', label: 'Domains', icon: 'fa-link', href: '/sub-account/digital-hub/domains' },
  { id: 'ai-seo', label: 'AI SEO', icon: 'fa-search', href: '/sub-account/digital-hub/ai-seo' },
  { id: 'client-portal', label: 'Client Portal', icon: 'fa-users', href: '/sub-account/digital-hub/client-portal' },
]

export default function DigitalHubLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const pathname = usePathname()

  // Determine which tab is active based on current path
  const getActiveTab = () => {
    // Exact match for overview
    if (pathname === '/sub-account/digital-hub') return 'overview'
    
    // Check sub-paths
    for (const tab of DIGITAL_HUB_TABS) {
      if (tab.href !== '/sub-account/digital-hub' && pathname.startsWith(tab.href)) {
        return tab.id
      }
    }
    return 'overview'
  }

  const activeTab = getActiveTab()

  return (
    <div className="space-y-6 -m-4 sm:-m-6 lg:-m-8">
      {/* Page Header with Tabs - Same structure as Marketing/Lead Engine */}
      <div className="flex-none bg-white border-b border-gray-200 px-4 sm:px-6 lg:px-8 pt-4 sm:pt-6 lg:pt-8 pb-0">
        {/* Header */}
        <div className="flex items-start justify-between mb-4">
          <div>
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 rounded-lg bg-gradient-to-br from-blue-500 to-indigo-600 flex items-center justify-center text-white shadow-lg">
                <i className="fa-solid fa-globe text-lg"></i>
              </div>
              <div>
                <h1 className="text-2xl font-bold text-gray-900">Digital Hub</h1>
                <p className="text-sm text-gray-500">Manage your digital assets, web presence, and online tools</p>
              </div>
            </div>
          </div>
          <div className="flex items-center gap-3">
            <button className="px-4 py-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-lg transition-colors flex items-center gap-2">
              <i className="fa-solid fa-cog"></i>
              <span className="hidden sm:inline">Settings</span>
            </button>
            <button className="px-4 py-2 bg-[var(--theme-secondary)] text-white rounded-lg hover:opacity-90 transition-all flex items-center gap-2 font-medium shadow-sm">
              <i className="fa-solid fa-plus"></i>
              <span>New Project</span>
            </button>
          </div>
        </div>
        
        {/* Tab Navigation - Horizontal scrollable tabs */}
        <nav className="flex items-center gap-1 -mb-px overflow-x-auto pb-px scrollbar-thin scrollbar-thumb-gray-300">
          {DIGITAL_HUB_TABS.map(tab => {
            const isActive = tab.id === activeTab
            return (
              <Link
                key={tab.id}
                href={tab.href}
                className={`py-3 px-3 text-sm font-medium border-b-2 transition-colors whitespace-nowrap flex items-center gap-2 ${
                  isActive
                    ? 'border-[var(--theme-secondary)] text-[var(--theme-secondary)]'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                <i className={`fa-solid ${tab.icon}`}></i>
                {tab.label}
              </Link>
            )
          })}
        </nav>
      </div>

      {/* Main Content Area - Full width without sidebar */}
      <div className="px-4 sm:px-6 lg:px-8 pb-4 sm:pb-6 lg:pb-8">
        {children}
      </div>
    </div>
  )
}
